@charset "UTF-8";
:root {
  --color-white: #ffffff;
  --color-dark-gray: #444440;
  --color-gray: #7e7e7b;
  --color-silver: #b9c9c7;
  --color-bisque: #f1dec8; }

/* ------------------------------------*|
  #SCSS-variables
/*------------------------------------ */
*,
*::before,
*::after {
  box-sizing: border-box; }

a {
  color: inherit;
  text-decoration: none; }

button {
  padding: 0;
  border: none;
  color: inherit;
  font: inherit;
  background: transparent; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.page {
  height: 100%;
  scroll-behavior: smooth; }

.page__body {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0;
  color: var(--color-dark-gray);
  font-weight: 400;
  font-size: 16px;
  font-family: "PT Sans", sans-serif;
  line-height: 1.4;
  background-color: var(--color-white); }

img {
  max-width: 100%;
  /* чтобы картинка не стала больше чем её контейнер */
  height: auto;
  /* картинка сохраняет пропорции */ }

ul {
  margin: 0;
  padding: 0;
  list-style: none; }

/* ------------------------------------*|
  #global classes
/*------------------------------------ */
.container {
  max-width: 1140px;
  margin: 0 auto;
  padding: 0 15px; }

.main {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 30px;
  padding-top: 50px;
  column-gap: 20px; }
  @media (max-width: 1150px) {
    .main {
      grid-template-columns: 1fr 1fr;
      row-gap: 50px;
      max-width: 800px;
      column-gap: 30px; } }
  @media (max-width: 620px) {
    .main {
      grid-template-columns: 1fr;
      row-gap: 50px; } }

/* ------------------------------------*|
  #buttons
/*------------------------------------ */
.section__link {
  color: var(--color-dark-gray);
  font-weight: normal;
  text-decoration: underline; }
  .section__link:hover, .section__link:focus {
    text-decoration: none; }

.section__title {
  max-width: 300px;
  margin: 0 auto;
  margin-bottom: 20px;
  padding: 5px 0;
  font-size: 25px;
  letter-spacing: 2px;
  text-align: center;
  text-transform: uppercase; }
  @media (max-width: 500px) {
    .section__title {
      max-width: 280px; } }
  .section__title--border {
    position: relative;
    z-index: 1;
    background-color: var(--color-white);
    outline: 2px solid var(--color-dark-gray); }
  .section__title--bg {
    position: relative;
    background-color: var(--color-bisque); }
    @media (max-width: 1150px) {
      .section__title--bg {
        background-color: transparent;
        background-color: var(--color-white);
        outline: 2px solid var(--color-dark-gray); } }
    .section__title--bg::after, .section__title--bg::before {
      position: absolute;
      width: 100%;
      height: 4px;
      content: "";
      background-color: var(--color-dark-gray); }
    .section__title--bg::before {
      top: calc(50% - 2px);
      left: -100%; }
    .section__title--bg::after {
      top: calc(50% - 2px);
      right: -100%; }
    @media (max-width: 1150px) {
      .section__title--bg {
        margin-bottom: 15px; }
        .section__title--bg::before, .section__title--bg::after {
          display: none; } }
  .section__title--central {
    position: relative;
    background-color: var(--color-bisque); }
    .section__title--central::after, .section__title--central::before {
      position: absolute;
      width: 100%;
      height: 4px;
      content: "";
      background-color: var(--color-dark-gray); }
    .section__title--central::before {
      top: calc(50% - 2px);
      left: -100%; }
    .section__title--central::after {
      top: calc(50% - 2px);
      right: -100%; }
    @media (max-width: 1150px) {
      .section__title--central::after, .section__title--central::before {
        width: 70%; }
      .section__title--central::before {
        left: -70%; }
      .section__title--central::after {
        right: -70%; } }
    @media (max-width: 750px) {
      .section__title--central::after, .section__title--central::before {
        width: 50%; }
      .section__title--central::before {
        left: -50%; }
      .section__title--central::after {
        right: -50%; } }
    @media (max-width: 620px) {
      .section__title--central::after, .section__title--central::before {
        width: 30%; }
      .section__title--central::before {
        left: -30%; }
      .section__title--central::after {
        right: -30%; } }
    @media (max-width: 500px) {
      .section__title--central::after, .section__title--central::before {
        display: none; } }

.section__text {
  margin: 0;
  color: var(--color-gray); }

.section__text + .section__text {
  margin-top: 10px; }

.section__list {
  margin: 0;
  padding: 0; }

.section__item {
  font-weight: bold;
  text-transform: uppercase; }

.section__item + .section__item {
  margin-top: 15px; }

.header {
  padding: 40px;
  background-color: var(--color-silver); }
  @media (max-width: 520px) {
    .header {
      padding: 30px 20px; } }

.header__title-box {
  width: 500px;
  margin: 0 auto;
  padding: 40px;
  border: 2px solid var(--color-dark-gray);
  text-align: center;
  background-color: var(--color-white);
  outline: 10px solid var(--color-white); }
  @media (max-width: 630px) {
    .header__title-box {
      width: 100%;
      padding: 20px; } }
  @media (max-width: 520px) {
    .header__title-box {
      width: auto;
      margin-right: 40px;
      padding: 20px; } }

.header__title {
  margin: 0;
  margin-bottom: 20px;
  color: var(--color-dark-gray);
  font-size: 35px;
  letter-spacing: 5px;
  text-transform: uppercase; }
  @media (max-width: 530px) {
    .header__title {
      margin-bottom: 10px;
      font-size: 19px;
      letter-spacing: 2px; } }
  @media (max-width: 330px) {
    .header__title {
      font-size: 18px; } }

.header__subtitle {
  margin: 0;
  color: var(--color-gray);
  font-size: 22px;
  font-family: "PT Serif", serif; }
  @media (max-width: 500px) {
    .header__subtitle {
      font-size: 15px; } }
  @media (max-width: 330px) {
    .header__subtitle {
      font-size: 14px; } }

.navigation {
  margin-bottom: 40px; }
  @media (max-width: 520px) {
    .navigation {
      margin-bottom: 0; } }

.navigation__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

.navigation__item + .navigation__item {
  margin-left: 10px; }

.navigation__link {
  display: block;
  padding: 5px;
  color: var(--color-dark-gray);
  text-transform: uppercase;
  transition: opacity 200ms ease 0s; }
  .navigation__link:hover, .navigation__link:focus {
    opacity: 0.6; }
  @media (max-width: 750px) {
    .navigation__link {
      font-size: 14px; } }

/* ------------------------------------*|
  #toggle
/*------------------------------------ */
@media (max-width: 520px) {
  .navigation--closed .navigation__list {
    display: none; } }

@media (max-width: 520px) {
  .navigation--opened .navigation__list {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    min-height: 320px;
    padding: 30px 0;
    background-color: var(--color-silver); } }

@media (max-width: 520px) {
  .navigation--opened .navigation__list .navigation__item + .navigation__item {
    margin-left: 0; } }

.navigation__toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 10;
  width: 36px;
  height: 36px;
  cursor: pointer; }

.navigation--closed .navigation__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 8px 0 0 #ffffff, 0 16px 0 0 #ffffff; }
  @media (min-width: 520px) {
    .navigation--closed .navigation__toggle::before {
      display: none; } }

.navigation--opened .navigation__toggle {
  top: 15px; }
  .navigation--opened .navigation__toggle::before, .navigation--opened .navigation__toggle::after {
    position: relative;
    top: 0;
    right: 0;
    z-index: 10;
    display: block;
    width: 36px;
    height: 2px;
    background-color: #ffffff;
    content: ""; }
  .navigation--opened .navigation__toggle::before {
    transform: rotate(45deg); }
  .navigation--opened .navigation__toggle::after {
    top: -2px;
    transform: rotate(-45deg); }
  @media (min-width: 520px) {
    .navigation--opened .navigation__toggle {
      display: none; } }

.photo {
  position: relative;
  width: 200px;
  height: 200px;
  margin: 0 auto;
  margin-top: 10px;
  border-radius: 50%;
  overflow: hidden; }
  @media (max-width: 1150px) {
    .photo {
      grid-row: 2/ 3;
      grid-column: 2 / 3;
      margin-top: 0; } }
  @media (max-width: 620px) {
    .photo {
      grid-row: 2 / 3;
      grid-column: 1 / 2; } }

.photo img {
  position: absolute;
  object-fit: cover;
  width: 120%;
  height: 120%; }

.contacts {
  padding-top: 30px; }
  @media (max-width: 1150px) {
    .contacts {
      grid-row: 1 / 3;
      grid-column: 2 / 3;
      padding-top: 55px; } }
  @media (max-width: 620px) {
    .contacts {
      padding-top: 0;
      grid-row: 3 / 4;
      grid-column: 1 / 2;
      width: 280px;
      margin: 0 auto; } }

.contacts__item {
  display: flex;
  justify-content: space-between; }
  .contacts__item:hover a,
  .contacts__item:focus a {
    opacity: 0.6; }
  .contacts__item::after {
    content: "";
    width: 20px;
    height: 20px;
    background-repeat: no-repeat;
    background-size: contain; }
  .contacts__item--mail::after {
    background-image: url("../img/icons/icon-mail.svg"); }
  .contacts__item--tel::after {
    background-image: url("../img/icons/icon-tel.svg"); }
  .contacts__item--address::after {
    background-image: url("../img/icons/icon-address.svg"); }
  .contacts__item--github::after {
    background-image: url("../img/icons/icon-gitHub.svg"); }
  .contacts__item--telegram::after {
    background-image: url("../img/icons/icon-telegram.svg"); }
  .contacts__item--discord::after {
    background-image: url("../img/icons/icon-discord.svg"); }

.contacts__item a {
  transition: opacity 200ms ease 0s; }

@media (max-width: 620px) {
